header {
  background-image: url("./img/망곰.png");
  height: 350px;
  background-color: rgb(124, 154, 209);
  color: rgb(59, 145, 245);
  padding: 1rem;
  text-align: center;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: center;
}

nav li {
  display: inline;
  margin-right: 10px;
}

nav a {
  color: rgb(59, 145, 245);
  text-decoration: none;
}

section {
  padding: 2rem;
  text-align: center;
}

footer {
  background-color: rgb(124, 154, 209);
  color: white;
  padding: 1rem;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
}